<template>
  <div>
    <Echart :options="options" id="air-quality-forecast" height="400px" width="340px"></Echart>
  </div>
</template>

<script>
import Echart from '@/common/echart'

export default {
  data() {
    return {
      options: {},

    };
  },
  components: {
    Echart,
  },
  props: {
    cdata: {
      type: Object,
      default: () => ({})
    },
  },
  watch: {
    cdata: {
      handler(nowData) {
        // 固定样式数据
        const spirit =
          'image://data:image/png;base64,';
        var maxData = 500;

        this.options = {

          tooltip: {},
          xAxis: {
            show: false,
            max: maxData,
            splitLine: { show: false },
            offset: 10,
            axisLine: {
              lineStyle: {
                color: '#0F84A5'
              }
            },
            axisLabel: {
              margin: 10,
              color: '#fff' // 将x轴上的字体颜色设置为白色
            },

          },
          yAxis: {
            data: nowData.time,//['6/7', '6/8', '6/9', '6/10', '11/11'],
            inverse: true,
            axisTick: { show: false },
            axisLine: { show: false },
            axisLabel: {
              margin: 10,
              color: '#999',
              fontSize: 16
            }
          },
          grid: {
            top: 'center',
            height: 300,
            left: 55,
            right: 95
          },
          series: [
            {
              // current data
              type: 'pictorialBar',
              symbol: spirit,
              symbolRepeat: 'fixed',
              symbolMargin: '5%',
              symbolClip: true,
              symbolSize: 30,
              symbolBoundingData: maxData,
              data: nowData.current_data,//[81, 20, 60, 70, 459],
              markLine: {
                symbol: 'none',
                label: {
                  formatter: 'max: {c}',
                  position: 'start',
                  color: '#fff',
                  fontSize: 18
                },
                lineStyle: {
                  color: 'green',
                  type: 'dotted',
                  opacity: 0.2,
                  width: 2,

                },
                data: [
                  {
                    type: 'max'
                  }
                ]
              },
              z: 10
            },
            {
              // full data
              type: 'pictorialBar',
              itemStyle: {
                opacity: 0.2
              },
              label: {
                show: true,
                opacity: 1,
                formatter: function (params) {
                  var value = params.value;
                  var level = '';
                  var color = '';

                  if (value >= 0 && value <= 50) {
                    level = '优';
                    color = 'green';
                  } else if (value >= 51 && value <= 100) {
                    level = '良';
                    color = 'yellow';
                  } else if (value >= 101 && value <= 150) {
                    level = '轻度污染';
                    color = 'orange';
                  } else if (value >= 151 && value <= 200) {
                    level = '中度污染';
                    color = 'red';
                  } else if (value >= 201 && value <= 300) {
                    level = '重度污染';
                    color = 'purple';
                  } else if (value >= 301 && value <= 500) {
                    level = '严重污染';
                    color = 'brown';
                  }

                  return `{${color}|${level}}`;
                },
                position: 'right',
                offset: [5, 0],
                rich: {
                  green: {
                    color: 'green',
                    fontSize: 22
                  },
                  yellow: {
                    fontSize: 22,
                    color: 'yellow'
                  },
                  orange: {
                    fontSize: 18,
                    color: 'orange'
                  },
                  red: {
                    fontSize: 18,
                    color: 'red'
                  },
                  purple: {
                    fontSize: 18,
                    color: '#99004C'
                  },
                  brown: {
                    fontSize: 18,
                    color: '#7E0023'
                  }
                },
                
              },
              animationDuration: 0,
              symbolRepeat: 'fixed',
              symbolMargin: '5%',
              symbol: spirit,
              symbolSize: 30,
              symbolBoundingData: maxData,
              data: nowData.true_data,//[81, 230, 60, 70, 41],
              z: 5
            }
          ]
        }
      },
      immediate: true,
      deep: true
    }
  }
};
</script>